body {
  background-color: rgb(15, 15, 15);
  user-select: none;
  color: white;
  font-family: "Courier New", Courier, monospace;
}

button {
  color: white;
}

label {
  margin: 0;
}

.hidden {
  display: none;
}

.App {
  text-align: center;
}

/* Grid */

.grid {
  max-width: fit-content;
  margin: auto;
  margin-top: 10px;
}

.square {
  border: 1px solid white;
  height: 25px;
  width: 25px;
}

.square:hover {
  scale: 120%;
  background-color: rgb(30, 30, 30);
  transition: 100ms;
  cursor: pointer;
}

.row {
  display: flex;
  gap: 0px;
}

/* Nodes */

.start-node,
.start-node:hover {
  background: transparent url("/public/images/play.svg") no-repeat center;
  background-size: 20px;
  background-color: rgb(15, 15, 15);
  animation: node-create 0.5s forwards;
}

.finish-node,
.finish-node:hover {
  background: transparent url("/public/images/target.svg") no-repeat center;
  background-size: 20px;
  background-color: rgb(15, 15, 15);
  animation: node-create 0.5s forwards;
}

.wall,
.wall:hover {
  background: transparent url("/public/images/wall.svg") no-repeat center;
  background-size: 25px;
  background-color: rgb(15, 15, 15);
  animation: node-create 0.5s forwards;
}

/* Nav Bar */

.nav {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 100px;
  gap: 0px;
}

.nav-text {
  font-size: 1rem;
}

.bottom-nav {
  display: flex;
  margin-top: 15px;
  justify-content: center;
}

.radio-section {
  translate: 0 0px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.button-section {
  height: 28px;
}

/* Nav Buttons */

.radio-button {
  opacity: 0;
  translate: 25px;
  margin: 0;
  z-index: -1;
  cursor: pointer;
}

.radio-label,
.button {
  text-align: center;
  border-radius: 5px;
  padding: 5px 10px;
  cursor: pointer;
  background-color: rgb(44, 44, 44);
  border: none;
  height: 38px;
  width: 200px;
  margin: 0;
  font-family: "Courier New", Courier, monospace;
}

.radio-label {
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(
    to right,
    rgb(60, 60, 255) 50%,
    rgb(44, 44, 44) 50%
  );
  background-size: 200%;
  background-position: right;
  height: 28px;
}

.radio-button {
  padding: 0;
  margin: 0;
}

.selected {
  animation: selector-change 0.5s forwards;
}

.button {
  color: white;
  margin-left: 15px;
}

.button:hover {
  background-color: rgb(80, 80, 80);
  transition: 800ms;
}

.find {
  background-color: darkgreen;
  width: 300px;
}

.find:hover {
  background-color: rgb(37, 165, 37);
}

/* DROPDOWN */

.dropdown-button {
  color: white;
  font-family: "Courier New", Courier, monospace;
  background-color: rgb(44, 44, 44);
  height: 38px;
  width: 200px;
  border-radius: 5px;
  cursor: pointer;
}

.dropdown-button-clicked {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

.dropdown-button p {
  margin: 5px;
  transform: translateY(5px);
}

.chosen-alg {
  display: block;
  text-align: center;
}

.down-arrow {
  display: block;
  text-align: right;
  transform: translateY(-18px);
  margin-right: 5px;
}

.dropdown {
  border-radius: 5px;
  position: relative;
  background-color: rgb(26, 26, 26);
  z-index: 9;
  width: 160px;
  height: 0;
  transform: translateY(-18px);
}

.dropdown-item {
  width: 80px;
  list-style-type: none;
  translate: 20px 15px;
  text-align: left;
  margin-top: 7px;
}

.dropdown-item:hover {
  cursor: pointer;
  transform: translateX(5px);
  color: rgb(0, 255, 0);
  transition: 100ms;
}

/* Pathing */

.shortest-path {
  animation: path-animate 500ms forwards;
}

.all-paths {
  animation: search-animate 1s forwards;
}

/* Animation Classes */

.drop-hide {
  animation: dropdown-reverse 500ms forwards;
}

.drop-show {
  animation: algorithm-dropdown 500ms forwards;
}

.item-show {
  animation: list-appear 0.5s 1 forwards;
}

/* Animation Keyframes */

@keyframes node-create {
  0% {
    background-size: 0%;
  }
  90% {
    background-size: 100%;
  }
  100% {
    background-size: 80%;
  }
}

@keyframes selector-change {
  0% {
    background-position: right;
  }
  100% {
    background-position: left;
  }
}

@keyframes algorithm-dropdown {
  0% {
    height: 0;
  }
  100% {
    height: 150px;
  }
}

@keyframes dropdown-reverse {
  0% {
    height: 150px;
  }
  100% {
    height: 0px;
    display: hidden;
  }
}

@keyframes list-appear {
  0% {
    opacity: 0;
  }
  75% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes path-animate {
  0% {
    background-color: rgb(115, 255, 0);
  }
  100% {
    background-color: rgb(0, 61, 0);
  }
}

@keyframes search-animate {
  0% {
    background-color: rgb(0, 179, 211);
  }
  100% {
    background-color: navy;
  }
}

/* Resolution Control */

@media (max-width: 1000px) {
  .square {
    width: 20px;
    height: 20px;
  }

  .start-node,
  .start-node:hover {
    background-size: 16px;
  }

  .finish-node,
  .finish-node:hover {
    background-size: 16px;
  }

  .wall,
  .wall:hover {
    background-size: 16px;
  }

  .radio-label,
  .button {
    height: 28px;
    width: 150px;
  }

  .radio-label {
    height: 18px;
  }

  .dropdown {
    width: 110px;
    transform: translateY(-28px);
  }

  .dropdown-item {
    translate: -5px 15px;
  }

  .dropdown-button {
    height: 28px;
    width: 150px;
  }

  .dropdown-button p {
    transform: translateY(0px);
  }

  .find {
    width: 200px;
  }
}

@media (max-width: 750px) {
  .radio-label,
  .button {
    height: 28px;
    width: 100px;
    font-size: 12px;
  }

  .radio-label {
    height: 18px;
  }

  .dropdown {
    width: 80px;
    transform: translateY(-20px);
  }

  .dropdown-item {
    translate: -8px 20px;
    font-size: 12px;
  }

  .dropdown-button {
    height: 28px;
    width: 120px;
  }

  .dropdown-button p {
    transform: translateY(2px);
    font-size: 12px;
  }

  .down-arrow {
    transform: translateY(-14px);
  }

  .find {
    width: 180px;
  }
}

@media (max-width: 500px) {
  .nav {
    height: 150px;
  }

  .square {
    width: 15px;
    height: 15px;
  }

  .start-node,
  .start-node:hover {
    background-size: 12px;
  }

  .finish-node,
  .finish-node:hover {
    background-size: 12px;
  }

  .wall,
  .wall:hover {
    background-size: 12px;
  }

  .radio-label,
  .button {
    height: 28px;
    width: 100px;
    font-size: 11px;
  }

  .radio-label {
    height: 18px;
    width: inherit;
  }

  .radio-section {
    transform: translateX(-7px);
  }

  .dropdown {
    width: 60px;
    transform: translateY(-20px);
  }

  .dropdown-item {
    translate: -18px 20px;
    font-size: 12px;
  }

  .dropdown-button {
    width: 100px;
  }

  .dropdown-button p {
    transform: translateY(3px);
  }
}
